body{
  margin-top: 25px;
  background-color: rgb(199, 199, 175);
}
.main-conteiner{
  min-width: 800px;
}

/* styles for table */

table {
  border-collapse: collapse;
  border: 1px solid black;
} 
th {
  border: 1px solid grey;
}
td {
  width: 5vw;
  height: 10vh;
  border: 1px solid black;
  text-align: center;
} 
.red{
  background-color: #a53923;
}
.need-to-check{
  background-color: #ea9999;
}
.green{
  background-color: #7fd161;
}
.yellow{
  background-color: #fff2cc;
}
.grey{
  background-color: #ccc;
}
.massage-warning{
  color: rgb(87, 38, 38);
}
.main-massage-warning{
  font-size: 16px;
  color: rgba(235, 17, 17, 0.856);
  line-height: 24px;
}

/* styles for input */

.input__select-mentor{
  width: 50%;
  min-width: 500px;
  margin: 0 auto;
}
input {
  width: 50%;
  height: 50px;
  margin-right: 20px;
  margin-bottom: 20px;
  padding-left: 20px;
  border-style: none;
  border: 1px solid green;
  border-radius: 10px;
  font-size: 24px;
  color: green;
}
input:focus{
  outline: none;
}
button{
  width: 100px;
  height: 50px;
  border-style: none;
  border-radius: 10px;
  background-color: rgb(121, 180, 33);
  color: white;
}
button:active{
  background-color: rgb(182, 182, 5);
}
button:focus{
  outline: none;
}
label {
  display: block;
  padding-left: 20px;
  margin-bottom: 15px;
  font-size: 24px;
  color: rgba(87, 38, 38, 0.753);
}
